"use client"

import { Header } from "@/components/header"
import { Footer } from "@/components/footer"
import { useState } from "react"
import { Filter, Star } from "lucide-react"

const CATEGORIES = ["Todos", "Moda", "Casa", "Acessórios", "Presentes"]
const PRICE_RANGES = ["Todas", "R$ 0-100", "R$ 100-300", "R$ 300-500", "R$ 500+"]

export default function ProductsPage() {
  const [selectedCategory, setSelectedCategory] = useState("Todos")
  const [selectedPrice, setSelectedPrice] = useState("Todas")
  const [sortBy, setSortBy] = useState("popular")

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      {/* Cabeçalho da Página */}
      <section className="py-8 px-4 bg-muted/30">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl md:text-4xl font-light text-foreground mb-2">Catálogo de Produtos</h1>
          <p className="text-foreground/70">Explore nossa coleção premium de produtos personalizáveis</p>
        </div>
      </section>

      <div className="flex-1 py-8 px-4">
        <div className="max-w-7xl mx-auto">
          <div className="grid grid-cols-1 lg:grid-cols-5 gap-8">
            {/* Filtros da Barra Lateral */}
            <div className="lg:col-span-1">
              <div className="sticky top-24 space-y-6">
                <div className="flex items-center space-x-2">
                  <Filter size={20} className="text-foreground" />
                  <h3 className="font-medium text-foreground">Filtros</h3>
                </div>

                {/* Categorias */}
                <div>
                  <h4 className="font-medium text-foreground mb-3 text-sm">Categoria</h4>
                  <div className="space-y-2">
                    {CATEGORIES.map((cat) => (
                      <label key={cat} className="flex items-center space-x-2 cursor-pointer">
                        <input
                          type="radio"
                          name="category"
                          value={cat}
                          checked={selectedCategory === cat}
                          onChange={(e) => setSelectedCategory(e.target.value)}
                          className="rounded-sm"
                        />
                        <span className="text-sm text-foreground/70">{cat}</span>
                      </label>
                    ))}
                  </div>
                </div>

                {/* Faixa de Preço */}
                <div>
                  <h4 className="font-medium text-foreground mb-3 text-sm">Preço</h4>
                  <div className="space-y-2">
                    {PRICE_RANGES.map((range) => (
                      <label key={range} className="flex items-center space-x-2 cursor-pointer">
                        <input
                          type="radio"
                          name="price"
                          value={range}
                          checked={selectedPrice === range}
                          onChange={(e) => setSelectedPrice(e.target.value)}
                          className="rounded-sm"
                        />
                        <span className="text-sm text-foreground/70">{range}</span>
                      </label>
                    ))}
                  </div>
                </div>

                {/* Resetar */}
                <button className="w-full py-2 border border-border rounded-lg text-sm font-medium text-foreground hover:bg-muted transition-colors">
                  Limpar Filtros
                </button>
              </div>
            </div>

            {/* Grade de Produtos */}
            <div className="lg:col-span-4">
              {/* Ordenar */}
              <div className="flex justify-between items-center mb-6">
                <p className="text-sm text-foreground/70">Mostrando 12 produtos</p>
                <div className="flex items-center space-x-2">
                  <label className="text-sm text-foreground/70">Ordenar:</label>
                  <select
                    value={sortBy}
                    onChange={(e) => setSortBy(e.target.value)}
                    className="px-3 py-1 border border-border rounded-lg text-sm bg-background text-foreground"
                  >
                    <option value="popular">Mais Popular</option>
                    <option value="newest">Mais Novo</option>
                    <option value="price-low">Menor Preço</option>
                    <option value="price-high">Maior Preço</option>
                  </select>
                </div>
              </div>

              {/* Produtos */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {Array(12)
                  .fill(null)
                  .map((_, idx) => (
                    <div key={idx} className="group cursor-pointer">
                      <div className="relative bg-muted rounded-lg overflow-hidden mb-4 aspect-square">
                        <img
                          src={`/product-customizable-.jpg?height=400&width=400&query=product customizable ${idx}`}
                          alt={`Produto ${idx + 1}`}
                          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                        />
                        <div className="absolute top-3 right-3 bg-accent text-accent-foreground px-3 py-1 rounded-full text-xs font-medium">
                          -20%
                        </div>
                      </div>
                      <div>
                        <h3 className="font-medium text-foreground group-hover:text-accent transition-colors">
                          Produto Premium {idx + 1}
                        </h3>
                        <p className="text-xs text-foreground/60 mt-1">Categoria</p>
                        <div className="flex items-center justify-between mt-3">
                          <div className="space-y-1">
                            <p className="text-sm text-foreground/50 line-through">R$ 159,90</p>
                            <p className="font-medium text-foreground">R$ 129,90</p>
                          </div>
                          <div className="flex items-center space-x-1">
                            <Star size={16} className="fill-accent text-accent" />
                            <span className="text-xs text-foreground/70">4.9</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>

              {/* Paginação */}
              <div className="flex justify-center items-center space-x-2 mt-12">
                <button className="px-3 py-2 border border-border rounded-lg text-sm font-medium hover:bg-muted transition-colors">
                  Anterior
                </button>
                <button className="px-3 py-2 bg-primary text-primary-foreground rounded-lg text-sm font-medium">
                  1
                </button>
                <button className="px-3 py-2 border border-border rounded-lg text-sm font-medium hover:bg-muted transition-colors">
                  2
                </button>
                <button className="px-3 py-2 border border-border rounded-lg text-sm font-medium hover:bg-muted transition-colors">
                  3
                </button>
                <button className="px-3 py-2 border border-border rounded-lg text-sm font-medium hover:bg-muted transition-colors">
                  Próximo
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  )
}
